.main-content{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.sumario{
  margin-bottom: 20px;
}

.botoes{
  width: 300px;
  margin: 20px;
  display: flex;
  flex-direction: column;
}

.btn{
  background-color: #F4F6FF;
  margin: 0px 5px;
  padding: 7px;
  border: none;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  transition: -webkit-filter 0.3s;
  transition: filter 0.3s;
  transition: filter 0.3s, -webkit-filter 0.3s;
}

.Next, .Previous{
  background-color: var(--color-amarelo);
}

.btn:hover{
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  /* text-decoration: underline; */
  cursor: pointer;
}

.btn:disabled{
  color: #000;
  background-color: rgb(185, 185, 185);
  cursor: auto;
  cursor: initial;
  opacity: 1;
}


.book-content{
  margin-right: 10px;
  /* display: hidden; */
}

.App-pages {
  height: 500px;
  width: 950px;
  perspective: 5000px;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.App-page {
  display: grid;
  grid-template: "esquerdo direito"
                / 1fr 1fr;
  color: white;
  height: 100%;
}

.btnDown{
  margin: 10px 0px;
  display: flex;
  justify-content: space-around;
}

img{
  width: 400px;
  max-height: 450px;
}
.footer{
  padding: 10px;
  background-color: var(--color-azulEscuro);
  color: var(--color-branco);
  display: flex;
  justify-content: space-evenly;
}

.footer li{
  font-size: 16px;
}
.content{
  background-color: var(--color-branco);
}
.main{
  height: 100vh;
}

header{
  background-color: var(--color-azulEscuro);
}

header .icons{
  color: var(--color-branco);
  margin-bottom: 10px;
  padding: 15px;
  display: flex;
  justify-content: center;
}

header i{
  margin: 0 10px;
  font-size: 1.5em;
}

.fa-laugh-beam{
  transition: color 0.3s;
}

.fa-laugh-beam:hover{
  color: var(--color-amarelo);
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  --color-azulEscuro: #10375C;
  --color-branco: #f4f6ff;
  --color-amarelo: #F3C623;
}

body {
  font: 400 14px PT Sans, sans-serif;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased; /* Suavização da fonte no chrome */
}

li{
  list-style: none;
}
